import '@kor-ui/kor';
import '@kor-ui/kor/kor-styles.css';
import {html} from 'lit';

export default {
  /** Generates the tab strip with the data binding.
   *  Each tab, when activated, changes a visibility of an appropriate content.
   *  It is impossible to make content elements as a child of a some element-wrapper,
   *  because in that case <kor-table> hasn't an apropriate overflow,
   *  thus detailed views should have no more than a one element-wrapper within
   *  the first <kor-grid> on <kor-page>, currently it is <div>,
   *  because custom elements can't be rendered dynamically
   *  into another custom elements directly (there is an error with a ShadowRoot).
   * @export @func tab_strip
   * @param {Object<string, Element>} summary
   * @param {Object<string, Element>} transactions
   * @param {Object<string, Element>} schedule
   * @return {Object}
   */
  tab_strip(summary, transactions, schedule) {
    return html`
      <kor-tab-item id="summaryTab" label="Summary" active
        @active-changed="${(e)=>{
          if (e.target.active) {
            Object.entries(transactions).map(
              (key_value) => {
                key_value[1].style.display = "none";
            });
            Object.entries(schedule).map(
              (key_value) => {
                key_value[1].style.display = "none";
            });
            Object.entries(summary).map(
              (key_value) => {
                key_value[1].style.display = "block";
            });
          }
        }}">
      </kor-tab-item>
      <kor-tab-item id="transactionsTab" label="Transactions"
        @active-changed="${(e)=>{
          if (e.target.active) {
            Object.entries(summary).map(
              (key_value) => {
                key_value[1].style.display = "none";
            });
            Object.entries(schedule).map(
              (key_value) => {
                key_value[1].style.display = "none";
            });
            Object.entries(transactions).map(
              (key_value) => {
                key_value[1].style.display = "block";
            });
          }
        }}">
      </kor-tab-item>
      <kor-tab-item id="scheduleTab" label="Schedule"
        @active-changed="${(e)=>{
          if (e.target.active) {
            Object.entries(summary).map(
              (key_value) => {
                key_value[1].style.display = "none";
            });
            Object.entries(transactions).map(
              (key_value) => {
                key_value[1].style.display = "none";
            });
            Object.entries(schedule).map(
              (key_value) => {
                key_value[1].style.display = "block";
            });
          }
        }}">
      </kor-tab-item>`;
  },
  /** Generate a table with two columns from Object.entries()
   *  array of [key, value] items
   * @export @func object_entries_two_columns
   * @param {Array<string, (string|number)>} object_entries_array
   * @return {Object}
   */
  object_entries_two_columns(object_entries_array) {
    return html`
      <kor-table columns="250px auto">
        <kor-table-row slot="header">
          <kor-table-cell head>Key</kor-table-cell>
          <kor-table-cell head>Value</kor-table-cell>
        </kor-table-row>
        ${object_entries_array.map(
          (key_value) => html`
            <kor-table-row>
              <kor-table-cell>${key_value[0]}</kor-table-cell>
              <kor-table-cell>${key_value[1]}</kor-table-cell>
            </kor-table-row>`)}
      </kor-table>`;
  },
  /** Generates a transactions view
   *
   * @callback transaction_details_callback
   * @param {Object} transaction
   *
   * @export @func transactions
   * @param {Array<Object>} in_transactions
   * @param {Array<Object>} out_transactions
   * @param {transaction_details_callback} transaction_details
   * @return {Object}
   */
  transactions(in_transactions, out_transactions, transaction_details) {
    return html`
      <kor-grid columns="2">
        <kor-card grid-cols="1" grid-cols-m="2" grid-cols-s="2" flat>
          <kor-text size="header-1">Incoming transactions</kor-text>
          <kor-table columns="1fr auto">
            <kor-table-row slot="header">
              <kor-table-cell head>From address</kor-table-cell>
              <kor-table-cell head>Amount in ETH</kor-table-cell>
            </kor-table-row>
            ${in_transactions.map(
              (transaction) => html`
                <kor-table-row @click="${(e)=>{transaction_details(transaction)}}">
                  <kor-table-cell>${transaction.attributes.from_address}</kor-table-cell>
                  <kor-table-cell>${(Number.parseInt(transaction.attributes.value)/Math.pow(10,18)).toFixed(18)}</kor-table-cell>
                </kor-table-row>`
            )}
          </kor-table>
        </kor-card>
        <kor-card grid-cols="1" grid-cols-m="2" grid-cols-s="2" flat>
          <kor-text size="header-1">Outgoing transactions</kor-text>
          <kor-table columns="1fr auto">
            <kor-table-row slot="header">
              <kor-table-cell head>To address</kor-table-cell>
              <kor-table-cell head>Amount in ETH</kor-table-cell>
            </kor-table-row>
            ${out_transactions.map(
              (transaction) => html`
                <kor-table-row @click="${(e)=>{transaction_details(transaction)}}">
                  <kor-table-cell>${transaction.attributes.to_address}</kor-table-cell>
                  <kor-table-cell>${(Number.parseInt(transaction.attributes.value)/Math.pow(10,18)).toFixed(18)}</kor-table-cell>
                </kor-table-row>`
            )}
          </kor-table>
        </kor-card>
      </kor-grid>`;
  },
  /** Generates a view to add a new transaction
   *  It is possible to choose whether to use a destination address
   *  from own addresses or from booked addresses or to enter a destination address manually
   *
   * @typedef {Object} TransactionData The reference to an object, where to store the results and get a defaults
   * @property {?string} destination_address The chosen destination address
   * @property {?number} amount The transaction amount in Eth with the parseFloat('1e-6') precision
   * @property {?number} gas_price The gas price in GWEI
   * @property {?number} gas_limit The maximum amount of gas, which can be used to process the transaction
   *
   * @export @func add_transaction Add a transaction
   * @param {number} account_balance The source address balance
   * @param {?Array<string>} own_addresses The list of own addresses
   * @param {?Array<string>} booked_addresses The list of booked addresses
   * @param {TransactionData} transaction_data The reference to an object, where to store the results and get a defaults
   * @return {Object}
   */
  add_transaction(account_balance, own_addresses, booked_addresses, transaction_data) {
    return html`
      <kor-accordion label="Add a destination address">
        <kor-radio-button label="Choose from own addresses" active
          @active-changed="${(e)=>{
            /** @const {Element} */
            const own_addresses_accordion =
              e.target.parentElement.querySelectorAll('kor-accordion')[0];
            if (e.target.active) {
              own_addresses_accordion.style.display = "block";
            } else {
              own_addresses_accordion.style.display = "none";
            }
          }}">
        </kor-radio-button>
        <kor-accordion label="The list of own addresses" icon="view_list" style="display: block">
          ${own_addresses.map(
            (own_address) => html`
              <kor-radio-button label="${own_address}"
                @active-changed="${(e)=>{
                  if (e.target.active) transaction_data.destination_address = own_address;
                }}">
              </kor-radio-button>`
          )}
        </kor-accordion>
        <kor-radio-button label="Choose from booked addresses"
          @active-changed="${(e)=>{
            /** @const {Element} */
            const booked_addresses_accordion =
              e.target.parentElement.querySelectorAll('kor-accordion')[1];
            if (e.target.active) {
              booked_addresses_accordion.style.display = "block";
            } else {
              booked_addresses_accordion.style.display = "none";
            }
          }}">
        </kor-radio-button>
        <kor-accordion label="The list of booked addresses" icon="view_list" style="display: none">
          ${booked_addresses.map(
            (booked_address) => html`
              <kor-radio-button label="${booked_address}"
                @active-changed="${(e)=>{
                  if (e.target.active) transaction_data.destination_address = booked_address;
                }}">
              </kor-radio-button>`
          )}
        </kor-accordion>
        <kor-radio-button label="Enter the destination address"
          @active-changed="${(e)=>{
            /** @const {Element} */
            const input_address =
              e.target.parentElement.querySelector('kor-input');
            if (e.target.active) {
              input_address.style.display = "block";
              transaction_data.destination_address = input_address.value;
            } else {
              input_address.style.display = "none";
            }
          }}">
        </kor-radio-button>
        <kor-input label="The destination address:" type="text" value="" style="display: none"
          @active-changed="${(e)=>{
            if (!e.target.active) transaction_data.destination_address = e.target.value;
          }}">
        </kor-input>
      </kor-accordion>
      <kor-input label="The transaction's amount in ETH:"
        type="number" value="0" min=0 max=${account_balance}
        @active-changed="${(e)=>{
          if (!e.target.active) transaction_data.amount = e.target.value;
        }}">
      </kor-input>
      <kor-input label="The transaction's gas price in GWEI:"
        type="number" value="${transaction_data.gas_price}" min=0
        @active-changed="${(e)=>{
          if (!e.target.active) transaction_data.gas_price = e.target.value;
        }}">
      </kor-input>
      <kor-input label="The transaction's gas limit:"
        type="number" value="${transaction_data.gas_limit}" min=0
        @active-changed="${(e)=>{
          if (!e.target.active) transaction_data.gas_limit = e.target.value;
        }}">
      </kor-input>`;
  },
  /** Generates a schedule view
   */
  schedule() {
    return html`

    `;
  }
}
